export default {
	data() {
		return {
			user: '',
			psw: '',
			isshow:false
		};
	},
	methods: {
		//登录
		login: function() {
			if (this.user && this.psw) {
				uni.request({
					url: '',
					data: {}
				});
			}else{
				this.isshow = true;
			}
		},
		//跳转到注册见面
		toSignUp: function() {
			uni.navigateTo({
				url: '../signup/signup'
			});
		},
		//获取用户名/邮箱
		getUser: function(e) {
			this.user = e.detail.value;
		},
		//获取密码
		getPassword: function(e) {
			this.psw = e.detail.value;
		}
	}
};
